<template>
	<!-- 内容平铺图 一行可放置 1、2、3张图片（100%，50%，33.33333%） -->
	<div
		class="clearfix"
		style="display: -webkit-box;display: -ms-flexbox;display: flex; width: 100%; -webkit-box-pack: justify;-ms-flex-pack: justify;justify-content: space-between"
		@mousedown="edit($event)"
	>
		<div
			style="display: block; -webkit-box-flex: 1 ;-ms-flex: 1; flex: 1;"
			v-for="(val, index) in items"
			:key="keyIndex(index)"
		>
			<template v-if="val">
				<a v-if="val.link" :href="val.link">
					<img :style="{ display: 'block', width: '100%' }" :src="val.url" />
				</a>
				<img
					v-else
					:style="{ display: 'block', width: '100%' }"
					:src="val.url"
				/>
			</template>
		</div>
	</div>
</template>

<script>
export default {
	props: {
		items: {
			type: Array,
			default: [{ url: "", link: "" }]
		},
		index: {
			type: Number,
			default: 0
		}
	},
	methods: {
		edit(e) {
			this.$emit("edit", e, this.index);
		},
		keyIndex(index) {
			return Math.random() + index;
		}
	}
};
</script>
