<template>
  <div>
    <div class="nav">
      <div class="content">
        <img src="../../../assets/image/iXiu/ixiu.jpg" width="50px" class="mt10" />
        <i class="fa fa-plus r pointer newH5" @click="showH5Editor()"></i>
      </div>
    </div>
    <div class="content">
      <div class="list">
        <table class="table-style">
          <tr>
            <th with="10%">名称</th>
            <th with="10%">url后缀</th>
            <th with="10%">负责人</th>
            <th with="10%">启动日期</th>
            <th with="10%">截止日期</th>
            <th with="10%">状态</th>
            <th with="10%">操作</th>
          </tr>
          <tr v-for="(val, index) in list" :key="index">
            <td width="20%">{{ val.activity_name }}</td>
            <td width="10%">{{ val.url_pc }}</td>
            <td width="10%">{{ val.person }}</td>
            <td width="10%">{{ val.start_time }}</td>
            <td width="10%">{{ val.end_time }}</td>
            <td width="10%" v-if="val.status == 0">未发布</td>
            <td width="10%" v-if="val.status == 1">已发布</td>
            <td width="10%" v-if="val.status == 2">已下线</td>
            <td width="10%">
              <router-link :to="{ path: '/iXiuEditor', query: { id: val.id } }">
                <i class="fa fa-mobile mr10 pointer"></i>
              </router-link>
              <i class="fa fa-edit pointer" style="font-size: 14px" @click="showH5Editor(val.id)"></i>
            </td>
          </tr>
        </table>
      </div>

      <!-- pagination | 分页 -->
      <div class="r mt30">
        <page :total="total" @turnpage="turnpage"></page>
      </div>
    </div>

    <!-- 添加/编辑活动 -->
    <promptbox title="H5活动信息" v-on:confirm="editActivityInfo(currId)" ref="editH5">
      <div class="h5-info">
        <div class="mb10">
          <p class="f13 mb4">* 活动名称</p>
          <input type="text" v-model.trim="h5.activity_name" class="ipt" />
          <i class="fa fa-check ml10 green" v-show="h5.activity_name != ''"></i>
        </div>
        <div class="mb10">
          <p class="f13 mb4">* 页面title</p>
          <input type="text" v-model.trim="h5.title" class="ipt" />
          <i class="fa fa-check ml10 green" v-show="h5.title != ''"></i>
        </div>
        <div class="mb10">
          <p class="f13 mb4">* url后缀</p>
          <input type="text" v-model.trim="h5.url_pc" class="ipt" />
          <i class="fa fa-check ml10 green" v-show="h5.url_pc != ''"></i>
        </div>
        <div class="mb10">
          <p class="f13 mb4">* 负责人</p>
          <input type="text" v-model.trim="h5.person" class="ipt" />
          <i class="fa fa-check ml10 green" v-show="h5.person != ''"></i>
        </div>
        <div class="mb10">
          <p class="f13 mb4">* 启动时间</p>
          <input type="date" v-model.trim="h5.start_time" class="ipt" />
          <i class="fa fa-check ml10 green" v-show="h5.start_time != ''"></i>
        </div>
        <div class="mb10">
          <p class="f13 mb4">* 结束时间</p>
          <input type="date" v-model.trim="h5.end_time" class="ipt" />
          <i class="fa fa-check ml10 green" v-show="h5.end_time != ''"></i>
        </div>
        <div class="mb10">
          <p class="f13 mb4">* 描述</p>
          <input type="text" v-model.trim="h5.description" class="ipt" placeholder="一句话描述" />
          <i class="fa fa-check ml10 green" v-show="h5.description != ''"></i>
        </div>
        <div class="mb10">
          <p class="f13 mb4">* 关键词</p>
          <input type="text" v-model.trim="h5.keyword" class="ipt" placeholder="请用英文逗号分隔" />
          <i class="fa fa-check ml10 green" v-show="h5.keyword != ''"></i>
        </div>
      </div>
    </promptbox>
  </div>
</template>

<script>
import 'font-awesome/css/font-awesome.min.css'
import promptbox from '../../../components/iXiu/PromptBox'
import pagination from '../../../components/iXiu/Pagination.vue'
import { getActivitiesList, editActivityInfo } from '../../../api/iXiu'

export default {
  name: 'Dashboard',
  data() {
    return {
      h5: {
        activity_name: '', // 活动名称
        title: '', // 页面title
        url_pc: '', // url后缀
        person: '', // 负责人
        start_time: '', // 启动时间
        end_time: '', // 结束时间
        description: '', // 描述
        keyword: '' // 关键词
      },
      list: [],
      total: 1,
      currId: '' // 当前操作对象的id
    }
  },
  components: {
    promptbox: promptbox,
    page: pagination
  },
  created() {
    this.getActivitiesList(1)
  },
  methods: {
    showH5Editor(id) {
      this.currId = id
      if (id) {
        for (var i = this.list.length - 1; i >= 0; i--) {
          if (this.list[i].id == id) {
            for (var key in this.h5) {
              this.h5[key] = this.list[i][key]
            }
          }
        }
      } else {
        this.currId = ''
        for (var key in this.h5) {
          this.h5[key] = ''
        }
      }
      this.$refs.editH5.show = true
    },

    submit() {
      for (var key in this.h5) {
        if (this.h5[key] == '') {
          this.$message.error('请将信息填写完整')
          return
        }
      }
      this.$message.success('保存成功')
      this.$refs.editH5.show = false
    },

    /**
     * 分页页码改变触发
     * @param {Number} 返回改变后的页码
     */
    turnpage(index) {
      this.getActivitiesList(index)
    },

    /**
     * 获取活动列表
     * @param {Number} page 页码
     */
    getActivitiesList(page) {
      getActivitiesList(page).then(res => {
        this.list = res.list
        this.total = res.paged.total / 10
      })
    },

    /**
     * 提交编辑后的活动信息
     * @param {Number} _id 活动id
     */
    editActivityInfo(_id) {
      for (var key in this.h5) {
        if (this.h5[key] === '') {
          return this.$message.error('请将信息填写完整')
        }
      }
      if (_id !== '') {
        this.h5.id = _id
      }
      editActivityInfo(this.h5).then(res => {
        if (res) {
          this.$refs.editH5.show = false
          this.getActivitiesList(1)
        } else {
          this.$message.error('网络错误，请稍后重试')
        }
      })
    }
  }
}
</script>

<style>
@import '../../../assets/style/iXiu/base.css';
</style>
<style scoped lang="scss">
@import './dashboard.scss';
</style>
