<template>
  <div class="container">
    <Loading v-show="showLoader"></Loading>

    <!-- 导航 -->
    <div class="nav">
      <div class="home" @click="goToDash">
        <i class="fa fa-home" style="font-size: 18px"></i>
      </div>
      <!-- 活动标题 -->
      <span class="mr10">{{ info.time }}</span>
      <span>{{ info.title }}</span>

      <!-- 调节按钮位置时的参数 -->
      <!-- <div class="indicator-wrapper" v-show="indicatorY >= 0">
        <span class="paramX">X: {{ indicatorX }} px</span>
        <span class="paramY">Y: {{ indicatorY }} px</span>
      </div> -->

      <span class="btn-group" v-if="activityStatus == 2 || activityStatus == 0">
        <button class="btn r mt8 mr15" @click="publish">
          <i class="fa fa-paper-plane"></i>
          保存并发布
        </button>
        <button class="btn r mt8 mr15" @click="preview">
          <i class="fa fa-eye"></i>
          预览
        </button>
        <button class="btn r mt8 mr15" @click="savePage">
          <i class="fa fa-cloud-upload"></i>
          保存
        </button>
      </span>

      <span class="btn-group" v-if="activityStatus == 1">
        <button class="btn r mt8 mr15" @click="offlineAct">
          <i class="fa fa-arrow-down"></i>
          下线活动
        </button>
        <button class="btn r mt8 mr15" @click="createQRCode">
          <i class="fa fa-magnet"></i>
          活动链接
        </button>
        <button class="btn r mt8 mr15" @click="publish">
          <i class="fa fa-cloud-upload"></i>
          保存并发布
        </button>
      </span>
    </div>

    <!-- 工具栏 -->
    <div class="tool-bar">
      <!-- <div class="tool" @click="showbgc">
        <i class="fa fa-hashtag"></i>
        <div class="tooltip">
          <span>背景色</span>
          <i></i>
        </div>
      </div> -->
      <div class="tool" @click="addImg">
        <i class="fa fa-image"></i>
        <div class="tooltip">
          <span>添加图片</span>
          <i></i>
        </div>
      </div>
      <!-- <div class="tool" @click="showEdit">
        <i class="fa fa-magnet"></i>
        <div class="tooltip">
          <span>添加按钮</span>
          <i></i>
        </div>
      </div> -->
      <!-- <div class="tool" @click="showHyperlink">
        <i class="fa fa-paper-plane"></i>
        <div class="tooltip">
          <span>添加超链接</span>
          <i></i>
        </div>
      </div> -->
      <!-- <div class="tool" @click="addBackImg">
        <i class="fa fa-globe" style="font-size: 18px"></i>
        <div class="tooltip">
          <span>添加背景图</span>
          <i></i>
        </div>
      </div> -->
      <!-- <div class="tool" @click="removeBackImg">
        <i class="fa fa-trash" style="font-size: 18px"></i>
        <div class="tooltip">
          <span>删除背景图</span>
          <i></i>
        </div>
      </div> -->

      <!-- PC 与 Moblie 切换
      <div class="checkout" @click="status = status == 1 ? 2 : 1">
        <i class="fa fa-mobile" style="font-size: 28px" v-show="status == 1"></i>
        <i class="fa fa-desktop" style="font-size: 20px" v-show="status == 2"></i>
      </div>
      -->
    </div>

    <!-- 编辑区-PC -->
    <div class="pc" v-show="status == 1" id="pc">
      <!-- 需取出提交的html代码 -->
      <div class="innerWrapper">
        <div
          style="background-repeat: round;
                    position: relative;"
          :style="{
            backgroundColor: pc.bgc,
            backgroundImage: 'url(' + pc.backImg + ')'
          }"
          class="pc-innerWrapper"
        >
          <!-- 大图 -->
          <div
            class="pc-img"
            v-for="(val, index) in pc.img"
            style="background-repeat: no-repeat;
                   width: 100%;
                   background-position: top center;"
            :style="{
              backgroundImage: 'url(' + val[0].url + ')',
              height: val[0].height + 'px'
            }"
            @mousedown="edit($event, index)"
            :key="index"
          ></div>

          <!-- 按钮 -->
          <div class="">
            <a
              :data-href="item.link"
              class="js-staticAct-btn"
              :data-type="item.type"
              v-for="(item, index) in pc.btn"
              target="_blank"
              style="display: block;
                     position: absolute;
                     left: 50%;
                     transform: translate(-50%);"
              @mousedown="editBtn($event, index)"
              :style="{ top: item.top + '%', marginLeft: item.left + 'px' }"
              :key="index"
            >
              <img :src="item.url" />
            </a>
          </div>

          <!--超链接-->
          <div class="hyperlink-wrapper">
            <a
              :data-href="item.hyperlinkUrl"
              target="_blank"
              class="a-link"
              v-for="(item, index) in pc.hyperlink"
              :key="index"
              style="display: block;
                      margin: 10px 0;
                      color: #333;
                      font-size: 14px;
                      position: absolute;
                      left: 50%;
                      cursor: pointer;
                      transform: translate(-50%);"
              @mousedown="editHyperlink($event, index)"
              :style="{
                top: item.top + '%',
                marginLeft: item.left + 'px',
                fontSize: item.hyperlinkFontSize + 'px',
                color: item.hyperlinkColor
              }"
            >
              <span>{{ item.hyperlinkText }}</span>
            </a>
          </div>
        </div>
      </div>
    </div>

    <!-- 编辑区-Mobile -->
    <div class="wrap" v-show="status == 2">
      <div class="mobile">
        <div class="screen" id="mobile">
          <div
            style="background-repeat:round; position:relative;"
            :style="{
              backgroundColor: mobile.bgc,
              backgroundImage: 'url(' + mobile.backImg + ')'
            }"
            class="innerWrapper mobile-innerWrapper"
          >
            <!-- 平铺图片 -->
            <ImgRow v-for="(val, index) in mobile.img" :items="val" :index="index" :key="index + 1" @edit="edit" />

            <!-- 按钮 -->
            <div class="btns-wrapper">
              <a
                v-for="(val, index) in mobile.btn"
                :data-href="val.link"
                :data-type="val.type"
                target="_blank"
                class="js-staticAct-btn"
                style="display:block; position: absolute; left: 50%; transform: translateX(-50%);"
                @mousedown="editBtn($event, index)"
                :style="{
                  top: val.top + '%',
                  marginLeft: val.left + 'px',
                  width: ((val.width * 100) / 750).toFixed(2) + '%'
                }"
                :key="index"
              >
                <img :src="val.url" style="display: block; width: 100%;" />
              </a>
            </div>

            <!--超链接-->
            <div class="links-wrapper">
              <a
                v-for="(item, index) in mobile.hyperlink"
                :href="item.hyperlinkUrl"
                target="_blank"
                class="a-link"
                style="display: block; margin: 10px 0; color: #333; font-size: 14px; position: absolute; left: 50%; transform: translate(-50%);"
                @mousedown="editHyperlink($event, index)"
                :style="{
                  top: item.top + '%',
                  marginLeft: item.left + 'px',
                  fontSize: item.hyperlinkFontSize + 'px',
                  color: item.hyperlinkColor
                }"
                :key="index"
                v-html="item.hyperlinkText"
              >
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 按钮参数弹框 -->
    <Promptbox title="按钮参数" v-on:confirm="addBtn(btnIndex)" ref="editBtn">
      <div>
        <!--APP按钮功能类型-->
        <div class="mb10 add-btn-app" v-show="status == 2">
          <p class="f13 mb10">* 按钮功能类型</p>
          <select name="" id="" v-model="btnParam.type">
            <option value="0">普通链接</option>
            <!-- <option value="1">APP直投项目列表页</option>
            <option value="2">APP优惠券页</option>
            <option value="3">APP邀请好友页</option>
            <option value="4">APP积分商城页</option>
            <option value="5">APP论坛首页</option>
            <option value="6">APP首投抽奖页面</option>
            <option value="7">APP债权项目列表页</option> -->
          </select>
          <div class="mt10" v-show="status == 2 && btnParam.type == 0">
            <p class="f13 mb4">* URL</p>
            <input type="text" v-model.trim="btnParam.link" class="ipt" placeholder="请输入绝对路径，以http://开头" />
            <i class="fa fa-check ml10 green" v-show="btnParam.link != ''"></i>
          </div>
        </div>

        <!--PC按钮功能类型-->
        <div class="mb10" v-show="status == 1">
          <p class="f13 mb4">* URL</p>
          <input type="text" v-model.trim="btnParam.link" class="ipt" placeholder="请输入绝对路径，以http://开头" />
          <i class="fa fa-check ml10 green" v-show="btnParam.link != ''"></i>
        </div>

        <div class="mb20 rel" style="height: 50px">
          <p class="f13 mb4">* 按钮图片（正常状态）</p>
          <button class="btn fakebtn">上传图片</button>
          <input
            type="file"
            @change="addBtnImg($event, 1)"
            accept="image/png, image/jpg, image/jpeg"
            class="btnUploader"
          />
          <i class="fa fa-check indicator" v-show="btnParam.url != ''"></i>
        </div>

        <div class="mb20 rel" style="height: 50px">
          <p class="f13 mb4">按钮图片（结束状态）</p>
          <button class="btn fakebtn">上传图片</button>
          <input
            type="file"
            @change="addBtnImg($event, 2)"
            accept="image/png, image/jpg, image/jpeg"
            class="btnUploader"
          />
          <i class="fa fa-check indicator" v-show="btnParam.url2 != ''"></i>
        </div>

        <div class="mb20 rel btn-during" style="height: 50px">
          <p class="f13 mb4">按钮时效</p>
          <ul class="mt10">
            <li class="mr20 mb8">
              <label for="非永久">
                <input type="radio" name="type" id="非永久" v-model="btnParam.persistent" value="0" />
                非永久
              </label>
            </li>
            <li class="mr20 mb8">
              <label for="永久">
                <input type="radio" name="type" id="永久" v-model="btnParam.persistent" value="1" />
                永久
              </label>
            </li>
          </ul>
        </div>
      </div>
    </Promptbox>

    <!-- 添加或者编辑图片 参数弹框 -->
    <Promptbox title="图片参数" v-on:confirm="saveImg" ref="editIMG">
      <div>
        <!--图片 h5可定义多列，PC只可1列-->
        <div class="mb20 rel btn-during" style="height: 50px">
          <p class="f13 mb10">* 列</p>
          <ul class="mt10">
            <li v-for="(val, index) in getSelectColumns" :key="index" class="mr20 mb8">
              <label :for="val['name']">
                <input
                  type="radio"
                  name="imgColType"
                  :id="val['name']"
                  v-model="imgParam.columns"
                  :value="val['value']"
                />
                {{ val['name'] }}
              </label>
            </li>
          </ul>
        </div>

        <!-- 图片上传 & 图片超链接 -->
        <div class="mb25" v-for="(val, index) in imgParam.imgs">
          <div class="mb6 rel" style="height: 60px">
            <p class="f13 mb4">* 图片</p>
            <button class="btn fakebtn">上传图片</button>
            <input
              type="file"
              @change="addUploadImg($event, index)"
              accept="image/png, image/jpg, image/jpeg"
              class="btnUploader"
            />
            <i class="fa fa-check indicator" v-show="val.url != ''"></i>
          </div>
          <div class="mb6">
            <p class="f13 mb4">URL</p>
            <input type="text" v-model.trim="val.link" class="ipt" placeholder="请输入绝对路径，以http://开头" />
          </div>
        </div>
      </div>
    </Promptbox>

    <!-- 图片功能菜单 -->
    <HandleMenu :e="eventObj" v-on:menuEvent="handleEvent" :menudata="menuForImg" ref="menu"> </HandleMenu>

    <!-- 按钮功能菜单栏 -->
    <HandleMenu :e="eventObj" v-on:menuEvent="handleBtnEvent" :menudata="menuForBtn" ref="menuForBtn"> </HandleMenu>

    <!--超链接功能菜单-->
    <HandleMenu :e="eventObj" v-on:menuEvent="handleHyperlinkEvent" :menudata="menuForHyperlink" ref="menuForHyperlink">
    </HandleMenu>

    <!-- 背景色 -->
    <Promptbox title="背景色" v-on:confirm="setbgc" ref="editbgc">
      <div>
        <p class="f13 mb4">色值</p>
        <input type="text" v-show="status === 1" v-model.trim="pc.bgc" class="ipt" v-focus @keyup.enter="setbgc" />
        <input type="text" v-show="status === 2" v-model.trim="mobile.bgc" class="ipt" v-focus @keyup.enter="setbgc" />
      </div>
    </Promptbox>

    <!-- 按钮水平偏移量 -->
    <Promptbox title="水平位置" v-on:confirm="setBtnOffset" ref="btnOffset">
      <div>
        <p class="f13 mb4">向{{ offsetType == 1 ? '左' : '右' }}偏移</p>
        <input type="text" v-model.trim="btnOffset" class="ipt" v-focus @keyup.enter="setBtnOffset" />
        <span> px</span>
      </div>
    </Promptbox>

    <!-- 超链接偏移量 -->
    <Promptbox title="水平位置" v-on:confirm="setHyperlinkOffset" ref="hyperlinkOffset">
      <div>
        <p class="f13 mb4">向{{ offsetType == 1 ? '左' : '右' }}偏移</p>
        <input type="text" v-model.trim="hyperlinkOffset" class="ipt" v-focus @keyup.enter="setHyperlinkOffset" />
        <span> px</span>
      </div>
    </Promptbox>

    <!--添加超链接文字-->
    <Promptbox title="添加超链接" ref="hyperlink" @confirm="setHyperlink(hyperlinkIndex)">
      <div>
        <div class="hyperlink-text hyperlink">
          <label for="">文字</label>
          <input class="ipt" type="text" v-model.trim="hyperlinkText" placeholder="输入超链接文字" />
        </div>
        <div class="hyperlink-address hyperlink">
          <label for="">链接</label>
          <input class="ipt" type="text" v-model.trim="hyperlinkUrl" placeholder="输入超链接，如http://xx.xx.com" />
        </div>
        <div class="hyperlink-address hyperlink">
          <label for="">颜色</label>
          <input class="ipt" type="text" v-model.trim="hyperlinkColor" placeholder="输入文字颜色，#开头" />
        </div>
        <div class="hyperlink-address hyperlink">
          <label for="">字号</label>
          <input class="ipt" type="text" v-model.trim="hyperlinkFontSize" placeholder="输入文字大小，不小于10px" />
        </div>
      </div>
    </Promptbox>

    <!-- 选择H5类型 
    <Promptbox title="选择H5类型" v-on:confirm="publish" ref="selectH5Type">
      <div>
        <ul>
          <li class="mr20 mb8">
            <label for="APP">
              <input id="APP" type="radio" name="type" v-model="html_wap_type" value="0" />
              APP
            </label>
          </li>
          <li class="mr20 mb8">
            <label for="WAP">
              <input id="WAP" type="radio" name="type" v-model="html_wap_type" value="1" />
              WAP
            </label>
          </li>
        </ul>
      </div>
    </Promptbox>
    -->

    <!--预览活动链接-->
    <div class="preview-modal" v-show="isShowPreviewModal">
      <div class="modal">
        <div class="close-btn">
          <i class="fa fa-times r" @click="isShowPreviewModal = false"></i>
        </div>
        <div class="head">
          <span>预览</span>
        </div>
        <div class="body">
          <!-- <div class="pc-url">
            <span>PC链接:</span>
            <a :href="url_pc" target="_blank">点我 &gt;_&lt;</a>
          </div> -->
          <div class="wap-url">
            <span>H5链接:</span>
            <input type="text" v-model="url_wap" />
            <div id="qrcode"></div>
          </div>
        </div>
        <div class="foot">
          <div class="button" @click="isShowPreviewModal = false">确定</div>
        </div>
      </div>
    </div>

    <!-- 背景图上传的隐藏input -->
    <input
      type="file"
      class="dn"
      id="backImgUploader"
      @change="uploadBackImg"
      accept="image/jpg, image/png, image/jpeg"
    />
  </div>
</template>

<script>
import 'font-awesome/css/font-awesome.min.css'
import handleMenu from '../../../components/iXiu/HandleMenu'
import promptbox from '../../../components/iXiu/PromptBox'
import loading from '../../../components/iXiu/Loading'
import QRCode from 'qrcodejs2'
import ImgRow from './children/ImgRow'
import { getEventInfo, saveEventInfo, publishEvent, previewEvent, offlineEvent } from '../../../api/iXiu'

export default {
  name: 'IxiuEditor',
  data() {
    return {
      info: {
        time: '',
        title: ''
      },
      // 图片菜单选项
      menuForImg: [
        {
          name: '在上方插入图片',
          eventkey: 1,
          iconname: 'arrow-up'
        },
        {
          name: '在下方插入图片',
          eventkey: 2,
          iconname: 'arrow-down'
        },
        {
          name: '删除',
          eventkey: 3,
          iconname: 'trash'
        }
      ],
      // 按钮菜单选项
      menuForBtn: [
        {
          name: '编辑按钮',
          eventkey: 1,
          iconname: 'edit'
        },
        {
          name: '删除按钮',
          eventkey: 2,
          iconname: 'trash'
        },
        {
          name: '向左偏移',
          eventkey: 3,
          iconname: 'arrow-left'
        },
        {
          name: '向右偏移',
          eventkey: 4,
          iconname: 'arrow-right'
        }
      ],
      // 超链接菜单选项
      menuForHyperlink: [
        {
          name: '编辑超链接',
          eventkey: 1,
          iconname: 'edit'
        },
        {
          name: '删除超链接',
          eventkey: 2,
          iconname: 'trash'
        },
        {
          name: '向左偏移',
          eventkey: 3,
          iconname: 'arrow-left'
        },
        {
          name: '向右偏移',
          eventkey: 4,
          iconname: 'arrow-right'
        }
      ],

      imgBaseUrl: '', // 图片域名

      status: 2, // 1: PC  2: Mobile

      imgColumns: [{ name: '1列', value: 1 }, { name: '2列', value: 2 }, { name: '3列', value: 3 }],
      imgParam: {
        // 图片编辑临时记录参数
        columns: 1, // 当前行图片 选择的列数
        imgs: [{ url: '', link: '' }] // 当前行的图片
      },

      btnParam: {
        // 按钮临时参数
        type: 0, // 按钮功能类型
        link: '', // 链接url
        top: '', // 垂直偏移量
        url: '', // 按钮图片1
        width: 0, // 按钮图片1的原始宽度
        url2: '', // 按钮图片2 活动结束后的按钮图片
        persistent: 0 // 按钮持续时效 0 非永久 1 永久
      },

      eventObj: null, // 传给弹出菜单handlemenu的事件对象
      imgIndex: '', // 选中状态的图片的index
      btnIndex: '', // 选中状态的按钮的index
      hyperlinkIndex: '', // 选中状态的超链接index
      insertType: '', // 插入图片方式 1: before  2: after else 最底部插入

      isInitPCFlag: false, // 是否是初始化PC数据标识
      isInitMobileFlag: false, // 是否是初始化Mobile数据标识

      isSaved: true, // 是否保存
      btnOffset: 0, // 按钮水平偏移量
      hyperlinkOffset: 0, // 超链接水平偏移量
      offsetType: 1, // 偏移类型 1-左  2-右
      indicatorX: 0, // 调节按钮垂直位置时的指示参数
      indicatorY: 0,

      hyperlinkText: '', // 超链接文本
      hyperlinkUrl: '', // 超链接url
      hyperlinkColor: '', // 超链接文字颜色
      hyperlinkFontSize: '', // 超链接文字大小

      showLoader: false, // 是否显示加载中
      activityStatus: '', // 活动上线下线状态 0 未发布 1 已发布 2 已下线

      isShowPreviewModal: false, // 是否显示预览模态框
      url_pc: '', // pc预览链接
      url_wap: '', // wap预览链接

      html_wap_type: 1, // 0 APP 1 WAP

      // 重新定义提交的对象
      id: '', // 活动id
      pc: {
        bgc: '', // 页面背景色
        backImg: '', // 页面背景图片
        img: [], // 二维数组，一行可能有多列图片，但是PC端没必要，故PC端限制死1列
        btn: [],
        hyperlink: []
      },
      mobile: {
        bgc: '', // 页面背景色
        backImg: '', // 页面背景图片
        img: [], // 二维数组，一行可能有多列图片
        btn: [],
        hyperlink: []
      }
    }
  },

  components: {
    HandleMenu: handleMenu,
    Promptbox: promptbox,
    Loading: loading,
    ImgRow: ImgRow
  },

  computed: {
    // 校验按钮url
    checkOutBtnUrl() {
      var reg = /^http:\/\//
      return reg.test(this.btnParam.link)
    },
    getSelectColumns() {
      let columns = this.imgColumns
      if (this.status == 1) {
        // pc
        columns = columns.slice(0, 1)
      }

      return columns
    }
  },

  created() {
    this.showLoader = true
    var itemId = this.$route.query.id
    this.getEventInfo(itemId)
  },

  // 浏览器前进后退时提醒保存
  beforeRouteLeave(to, from, next) {
    if (this.isSaved) {
      return next()
    }
    const answer = window.confirm('当前页面数据尚未保存，是否离开？')
    if (answer) {
      next()
    } else {
      next(false)
    }
  },

  mounted() {
    var me = this
    window.oncontextmenu = () => false
    window.onbeforeunload = e => {
      if (me.$route.path === '/editor' && !me.isSaved) {
        var e = window.event || e
        e.returnValue = '确定离开当前页面吗'
      }
    }
  },

  methods: {
    /* 页面图片操作 */
    /* 新增图片 */
    addImg() {
      // 插入位置为 当前图片流的最底部
      this.insertType = ''
      // 当前修改图片的 index
      this.imgIndex = ''
      // reset imgParam
      this.imgParam.columns = 1
      this.imgParam.imgs = [{ url: '', link: '' }]
      // 弹出弹窗
      this.$refs.editIMG.show = true
    },
    // 上传图片
    addUploadImg(e, index) {
      var file = e.target.files[0]
      e.target.value = null
      if (!file) return

      this.showLoader = true
      this.uploadImg(file).then(res => {
        this.showLoader = false
        this.isSaved = false

        const imgUrl = res.data.data.file_domain + '/' + res.data.data.file_url
        this.imgParam.imgs[index]['url'] = imgUrl
        this.getIMGsize(imgUrl).then(res => {
          this.imgParam.imgs[index]['height'] = res.height // PC端需要图片高度
        })
      })
    },
    // 保存当前编辑的图片条目
    saveImg() {
      // 校验表单数据 url必填，link非必填
      const validate = this.imgParam.imgs.every(e => {
        return e.url != ''
      })
      if (!validate) {
        this.$message.error('请将图片上传完整')
        return
      }

      let target = this.status == 1 ? this.pc.img : this.mobile.img
      if (this.insertType == 1) {
        // 在前方插入
        target.splice(this.imgIndex, 0, this.imgParam.imgs)
      } else if (this.insertType == 2) {
        // 在后方插入
        target.splice(this.imgIndex + 1, 0, this.imgParam.imgs)
      } else {
        // 在尾部插入
        target.push(this.imgParam.imgs)
      }

      // 关闭弹窗
      this.$refs.editIMG.show = false
    },
    // 编辑选择的图片条目
    edit(e, i) {
      e.preventDefault()
      if (e.button == 2) {
        this.eventObj = e
        this.imgIndex = i
        this.$refs.menu.show = true
      }
    },
    // 处理图片处理菜单里边的 上方插入、下方插入、移除
    handleEvent(eventkey) {
      switch (eventkey) {
        case 1: // 上方插入
          this.insertType = 1
          // reset imgParam
          this.imgParam.columns = 1
          this.imgParam.imgs = [{ url: '', link: '' }]
          // 弹出弹窗
          this.$refs.editIMG.show = true
          break
        case 2: // 下方插入
          this.insertType = 2
          // reset imgParam
          this.imgParam.columns = 1
          this.imgParam.imgs = [{ url: '', link: '' }]
          // 弹出弹窗
          this.$refs.editIMG.show = true
          break
        case 3: // 移除
          this.removeImg()
          break
        case 4:
          this.showHyperlink()
          break
      }
    },
    removeImg() {
      let i = this.imgIndex
      let target = this.status == 1 ? this.pc.img : this.mobile.img

      target.splice(i, 1)
    },
    // 显示超链接输入框
    showHyperlink() {
      this.hyperlinkIndex = ''
      this.hyperlinkText = this.hyperlinkUrl = ''
      this.$refs.hyperlink.show = true
    },
    // 设置超链接
    setHyperlink(index) {
      var obj = {}
      obj.hyperlinkText = this.hyperlinkText
      obj.hyperlinkUrl = this.hyperlinkUrl
      obj.hyperlinkColor = this.hyperlinkColor
      obj.hyperlinkFontSize = this.hyperlinkFontSize

      var target = this.status == 1 ? this.pc.hyperlink : this.mobile.hyperlink
      var parent = this.status == 1 ? 'pc' : 'mobile'

      if (
        obj.hyperlinkText === '' ||
        obj.hyperlinkUrl === '' ||
        obj.hyperlinkColor === '' ||
        obj.hyperlinkFontSize === ''
      ) {
        return this.$message.error('请将参数填写完整')
      }

      // 不存在index为添加，存在index为编辑
      if (index === '' || index < 0) {
        var t = document.getElementById(parent).scrollTop
        obj.top = 0
        obj.left = 0
        target.push(obj)
      } else {
        target[index].hyperlinkText = this.hyperlinkText
        target[index].hyperlinkUrl = this.hyperlinkUrl
        target[index].hyperlinkColor = this.hyperlinkColor
        target[index].hyperlinkFontSize = this.hyperlinkFontSize
      }

      this.$refs.hyperlink.show = false
    },
    // 编辑超链接
    editHyperlink(e, i) {
      e.preventDefault()
      var target = this.status == 1 ? this.pc.hyperlink : this.mobile.hyperlink
      if (e.button == 2) {
        this.eventObj = e
        this.hyperlinkIndex = i
        this.$refs.menuForHyperlink.show = true
      } else {
        this.drag(e, i, target)
      }
    },
    // 超链接处理函数
    handleHyperlinkEvent(eventkey) {
      var target = this.status == 1 ? this.pc.hyperlink : this.mobile.hyperlink
      switch (eventkey) {
        case 1:
          this.hyperlinkText = target[this.hyperlinkIndex].hyperlinkText
          this.hyperlinkUrl = target[this.hyperlinkIndex].hyperlinkUrl
          this.hyperlinkColor = target[this.hyperlinkIndex].hyperlinkColor
          this.hyperlinkFontSize = target[this.hyperlinkIndex].hyperlinkFontSize
          this.$refs.hyperlink.show = true
          break
        case 2:
          target.splice(this.hyperlinkIndex, 1)
          break
        case 3:
          var i = this.hyperlinkIndex
          this.hyperlinkOffset = Math.abs(target[i].left)
          this.offsetType = 1
          this.$refs.hyperlinkOffset.show = true
          break
        case 4:
          var i = this.hyperlinkIndex
          this.hyperlinkOffset = Math.abs(target[i].left)
          this.offsetType = 2
          this.$refs.hyperlinkOffset.show = true
          break
      }
    },
    // 设置超链接偏移
    setHyperlinkOffset() {
      var i = this.hyperlinkIndex
      var target = this.status == 1 ? this.pc.hyperlink : this.mobile.hyperlink
      var num = parseInt(this.hyperlinkOffset)
      if (isNaN(this.hyperlinkOffset) || num < 0) {
        return this.$message.error('请设置正整数')
      }
      if (this.offsetType == 1) {
        target[i].left = -num
      } else {
        target[i].left = num
      }
      this.$refs.hyperlinkOffset.show = false
    },

    /* 按钮操作 */
    showEdit() {
      this.btnIndex = ''
      this.$refs.editBtn.show = true
    },
    addBtn(index) {
      var target = this.status == 1 ? this.pc.btn : this.mobile.btn
      var parent = this.status == 1 ? 'pc' : 'mobile'
      var obj = { left: 0 }

      // 参数校验
      if (this.status == 1 && !this.btnParam.link) {
        this.$message.error('缺少按钮URL')
        return
      }
      if (!this.btnParam.url || !this.btnParam.url2) {
        this.$message.error('请上传按钮图片')
        return
      }
      if (this.status == 2 && !this.btnParam.link && this.btnParam.type == 0) {
        this.$message.error('缺少按钮URL')
        return
      }

      // index存在，则表示编辑，不存在则表示添加
      if (index !== '') {
        // 复制按钮参数对象
        for (var key in this.btnParam) {
          target[index][key] = this.btnParam[key]
          this.btnParam[key] = '' // 复原按钮参数对象
        }
      } else {
        // 复制按钮参数对象
        for (var key in this.btnParam) {
          obj[key] = this.btnParam[key]
          this.btnParam[key] = '' // 复原按钮参数对象
        }

        if (this.status == 1) {
          obj.type = 0
        }

        // 设置按钮初始高度
        var t = document.getElementById(parent).scrollTop
        obj.top = 0
        obj.left = 0
        target.push(obj)
      }

      this.btnParam.persistent = 0

      // 关闭弹框
      this.$refs.editBtn.show = false
    },
    addBtnImg(e, type) {
      var file = e.target.files[0]
      e.target.value = null
      if (!file) return

      this.showLoader = true
      this.uploadImg(file).then(res => {
        if (res.data.code == 0) {
          this.showLoader = false
          this.isSaved = false
          if (type == 1) {
            this.btnParam.url = res.data.data.file_domain + '/' + res.data.data.file_url
            this.getIMGsize(this.btnParam.url).then(res => {
              this.btnParam.width = res.width
            })
          }
          if (type == 2) {
            this.btnParam.url2 = res.data.data.file_domain + '/' + res.data.data.file_url
          }
        }
      })
    },

    // 按钮拖拽
    drag(e, i, targetObj) {
      var me = this
      var el = e.currentTarget
      var offsetTop = el.parentNode.parentNode.getBoundingClientRect().top // 元素父节点相对于视窗的高度
      var target = targetObj[i]

      var className = this.status === 1 ? 'pc-innerWrapper' : 'mobile-innerWrapper'

      var wrapper = document.getElementsByClassName(className)[0]
      var wrapperHeight = parseInt(window.getComputedStyle(wrapper).height)

      var move = function(e) {
        e.stopPropagation()
        e.preventDefault()

        // var elOffsetTop = el.offsetTop;
        // var clientToButton = e.pageY - 50 - elOffsetTop    // 鼠标距离按钮顶部高度

        target.top = ((e.pageY - offsetTop) / wrapperHeight) * 100
        this.indicatorOfBtn = target.top
      }.bind(this)

      var clear = function() {
        this.indicatorOfBtn = 0
        document.removeEventListener('mousemove', move, false)
        document.removeEventListener('mouseup', clear, false)
      }.bind(this)

      document.addEventListener('mousemove', move, false)
      document.addEventListener('mouseup', clear, false)
    },
    editBtn(e, i) {
      e.preventDefault()
      var target = this.status == 1 ? this.pc.btn : this.mobile.btn
      if (e.button == 2) {
        this.eventObj = e
        this.btnIndex = i
        this.$refs.menuForBtn.show = true
      } else {
        this.drag(e, i, target)
      }
    },
    handleBtnEvent(eventkey) {
      switch (eventkey) {
        case 1:
          var i = this.btnIndex
          var btn = this.status == 1 ? this.pc.btn : this.mobile.btn
          for (var key in this.btnParam) {
            this.btnParam[key] = btn[i][key]
          }
          this.$refs.editBtn.show = true
          break
        case 2:
          var i = this.btnIndex
          var btn = this.status == 1 ? this.pc.btn : this.mobile.btn
          btn.splice(i, 1)
          break
        case 3:
          var i = this.btnIndex
          var btn = this.status == 1 ? this.pc.btn : this.mobile.btn
          this.btnOffset = Math.abs(btn[i].left)
          this.offsetType = 1
          this.$refs.btnOffset.show = true
          break
        case 4:
          var i = this.btnIndex
          var btn = this.status == 1 ? this.pc.btn : this.mobile.btn
          this.btnOffset = Math.abs(btn[i].left)
          this.offsetType = 2
          this.$refs.btnOffset.show = true
      }
    },
    setBtnOffset() {
      var i = this.btnIndex
      var btn = this.status == 1 ? this.pc.btn : this.mobile.btn
      var num = parseInt(this.btnOffset)
      if (isNaN(this.btnOffset) || num < 0) {
        return this.$message.error('请设置正整数')
      }
      if (this.offsetType == 1) {
        btn[i].left = -num
      } else {
        btn[i].left = num
      }
      this.$refs.btnOffset.show = false
    },

    /* 背景色 */
    showbgc() {
      this.$refs.editbgc.show = true
    },
    setbgc() {
      this.$refs.editbgc.show = false
    },

    /* 背景图 */
    addBackImg() {
      document.getElementById('backImgUploader').click()
    },
    uploadBackImg() {
      var uploader = document.getElementById('backImgUploader')
      var file = uploader.files[0]
      uploader.value = null
      if (!file) return

      this.showLoader = true
      this.uploadImg(file).then(res => {
        if (res.data.code == 0) {
          this.showLoader = false
          this.isSaved = false
          var target = this.status == 1 ? this.pc : this.mobile
          target.backImg = res.data.data.file_domain + '/' + res.data.data.file_url
        }
      })
    },
    removeBackImg() {
      if (this.status == 1) {
        this.pc.backImg = ''
      } else {
        this.mobile.backImg = ''
      }
    },

    /* 回到列表页 */
    goToDash() {
      if (this.isSaved) {
        this.$router.push('/')
      } else {
        this.$message.error('请先保存')
      }
    },

    // 选择H5类型弹框
    showSelectH5Type: function() {
      this.$refs.selectH5Type.show = true
    },

    // 获取活动信息
    getEventInfo: function(_id) {
      getEventInfo(_id).then(res => {
        this.showLoader = false
        var _d = res
        this.id = _d.id
        this.activityStatus = _d.status
        this.info.title = _d.activity_name
        this.info.time = _d.end_time
        this.pc = _d.pc
        this.mobile = _d.mobile
        // set H5 image width
        this.mobile.btn.forEach(element => {
          this.getIMGsize(element.url).then(res => {
            element.width = res.width
          })
        })
        this.url_pc = _d.url_pc
        this.url_wap = _d.url_wap
        this.isSaved = true
        this.isInitPCFlag = true
        this.isInitMobileFlag = true
      })
    },

    // 上传图片
    uploadImg: function(file) {
      var image = new FormData()
      image.append('file', file)
      const instance = this.axios.create({
        headers: { 'Content-Type': 'multipart/form-data', 'X-ADMIN-Authorization': window.token }
      })

      return new Promise((resolve, reject) => {
        instance.post(process.env.VUE_APP_APPSERVER_HOST + '/admin/api.static.img.upload', image).then(
          res => {
            if (0 != res.data.code) {
              this.$message.error(res.data.info)
              reject(res)
            } else {
              resolve(res)
            }
          },
          error => {
            this.$message.error(error)
            reject(error)
          }
        )
      })
    },

    // 获取图片尺寸大小
    getIMGsize: function(url) {
      return new Promise((resolve, reject) => {
        var img = new Image()
        img.onload = () => {
          resolve({ width: img.width, height: img.height })
        }
        img.src = url
      })
    },

    // 保存页面动作
    savePage: function() {
      this.showLoader = true
      this.ajaxSavePage().then(res => {
        this.showLoader = false
        if (res) {
          this.isSaved = true
          this.$message.success('保存成功！')
        } else {
          this.$message.success('保存失败，请稍后重试！')
        }
      })
    },

    // 保存页面ajax接口
    ajaxSavePage: function() {
      var paramObj = {}
      paramObj.id = this.id
      paramObj.pc = this.pc
      paramObj.mobile = this.mobile
      return saveEventInfo(paramObj)
    },

    // 保存并发布
    async publish() {
      this.showLoader = true

      // 保存
      if (!this.isSaved) {
        const resSave = await this.ajaxSavePage()
      }
      // 发布
      const resPub = await this.api_publish()
      this.showLoader = false

      if (resPub) {
        this.$message.success('发布成功！')
        this.url_pc = resPub.data.url_pc
        this.url_wap = resPub.data.url_wap
        this.getEventInfo(this.id)
      } else {
        this.$message.error('发布失败，请稍后重试！')
      }
    },

    api_publish: function() {
      var paramObj = {}
      paramObj.id = this.id
      // paramObj.html_pc = document.getElementById('pc').innerHTML
      paramObj.html_wap = document.getElementById('mobile').innerHTML
      paramObj.html_wap_type = this.html_wap_type
      return publishEvent(paramObj)
    },

    showPreviewModal: function() {
      this.$refs.previewModal.show = true
    },

    // 预览
    preview: function() {
      if (!this.isSaved) {
        this.$message.error('请先保存')
        return
      }
      this.showLoader = true
      var paramObj = {}
      paramObj.id = this.id
      paramObj.html_pc = document.getElementById('pc').innerHTML
      paramObj.html_wap = document.getElementById('mobile').innerHTML
      paramObj.html_wap_type = this.html_wap_type
      previewEvent(paramObj).then(res => {
        this.showLoader = false
        if (res) {
          this.url_pc = res.data.url_pc
          this.url_wap = res.data.url_wap
          this.createQRCode()
        } else {
          this.$message.error('预览失败，请稍后重试！')
        }
      })
    },

    // 下线活动
    offlineAct: function() {
      this.showLoader = true
      offlineEvent(this.id).then(res => {
        this.showLoader = false
        if (res) {
          this.$message.success('下线成功！')
          this.getEventInfo(this.id)
        } else {
          this.$message.error('下线失败，请稍后重试！')
        }
      })
    },

    // 生成二维码
    createQRCode() {
      var _this = this
      _this.isShowPreviewModal = true
      var wrapper = document.getElementById('qrcode')
      while (wrapper.hasChildNodes()) {
        wrapper.removeChild(wrapper.firstChild)
      }
      new QRCode(wrapper, {
        width: 120,
        height: 120,
        text: _this.url_wap
      })
    }
  },

  // 监听数据变化，提示操作者及时保存
  watch: {
    'imgParam.columns': function(val, oldVal) {
      let res = this.imgParam.imgs.slice(0)
      if (val > this.imgParam.imgs.length) {
        for (let i = 0; i < val - this.imgParam.imgs.length; i++) {
          res.push({ url: '', link: '' })
        }
      } else {
        res = this.imgParam.imgs.slice(0, val)
      }
      this.imgParam.imgs = res
    },
    // pc: {
    //   handler(val, oldVal) {
    //     if (!this.isInitPCFlag) {
    //       this.isSaved = false
    //       console.log('pc object is changed')
    //     } else {
    //       this.isInitPCFlag = false
    //     }
    //   },
    //   deep: true
    // },
    mobile: {
      handler(val, oldVal) {
        if (!this.isInitMobileFlag) {
          this.isSaved = false
          console.log('mobile object is changed')
        } else {
          this.isInitMobileFlag = false
        }
      },
      deep: true
    }
  }
}
</script>

<style>
@import '../../../assets/style/iXiu/base.css';
</style>
<style lang="scss" scoped>
@import './editor.scss';
</style>
