import { fetchEndpoint } from '../server/network'

// 获取活动列表
export const getActivitiesList = page =>
  fetchEndpoint('/admin/api.static.activity.list', 'POST', {
    page: page,
    size: 10
  })

// 更新活动主信息
export const editActivityInfo = params => fetchEndpoint('/admin/api.static.activity.save', 'POST', params)

// 获取活动页面详情
export const getEventInfo = id =>
  fetchEndpoint('/admin/api.static.activity.detail', 'POST', {
    id: id,
    need_resource_info: 1
  })

// 保存活动页面信息
export const saveEventInfo = params => fetchEndpoint('/admin/api.static.page.save', 'POST', params)

// 发布活动
export const publishEvent = params => fetchEndpoint('/admin/api.static.activity.publish', 'POST', params)

// 预览活动页
export const previewEvent = params => fetchEndpoint('/admin/api.static.activity.preview', 'POST', params)

// 下线活动页
export const offlineEvent = id => fetchEndpoint('/admin/api.static.activity.offline', 'POST', { id: id })
